<template>
  <div id="me" style="background:#ccc; height:100vh">
    <div style="height:3rem;background:#fff;">
      <div style="float:right;padding-top:0.3rem">
        <i class="iconfont icon-xiangji"></i>
        <i class="iconfont icon--ellipsis" style="padding:0 0.5rem"></i>
      </div>
    </div>
    <chat style="padding:1rem 0.5rem">
      <div slot="char_img">
        <a href="#/about">
          <img
            width="100%"
            src="https://tse1-mm.cn.bing.net/th?id=OIP.6S3laUK8U0-vo9LL3enR0QAAAA&w=148&h=150&c=7&o=5&dpr=2&pid=1.7"
            alt
          />
        </a>
      </div>
      <div slot="name">MT</div>
      <div slot="info">
        <a href="#/about">微信号：mt659</a>
      </div>
      <div slot="time">
        <a href="#/about" style="font-size:1.5rem;position:relative;top:1rem">></a>
      </div>
    </chat>
    <div>
      <icon class="icon_s" v-for="i in list">
        <div slot="icon">
          <svg class="icon" aria-hidden="true">
            <use :xlink:href="i.icon" />
          </svg>
        </div>
        <div slot="title">{{i.title}}</div>
        <div slot="more">></div>
      </icon>
    </div>
  </div>
</template>

<script>
import chat from "./../../template/chat";
import icon from "./../../template/icon";
export default {
  components: {
    chat,
    icon
  },
  data() {
    return {
      list: [
        { icon: "#icon-zhifu-copy", title: "支付" },
        { icon: "#icon-wechatEnshrine-copy", title: "收藏" },
        { icon: "#icon-xiangce1-copy", title: "相册" },
        { icon: "#icon-qiabao-copy", title: "卡包" },
        { icon: "#icon-biaoqing-copy-copy", title: "表情" },
        { icon: "#icon-shezhi-copy", title: "设置" }
      ]
    };
  }
};
</script>

<style>
#me a {
  display: block;
}
#me .icon_s .icon {
  font-size: 2rem;
  position: relative;
  top: -0.6rem;
}
#me .icon_s:first-child {
  margin: 0.75rem 0;
}
#me .icon_s .right {
  border-bottom: 1px solid #ccc;
}
#me .icon_s {
  background: #fff;
}
</style>


