<template>
  <div style="display:flex;padding-left:0.5rem">
    <div class="left">
      <div>
        <slot name="icon"></slot>
      </div>
    </div>
    <div class="right">
      <div style="flex:1;line-height:4.3rem">
        <slot name="title"></slot>
      </div>
      <div style="margin-right:1rem;display:flex;line-height:2rem;padding-top:1.2rem">
        <div style="width:2rem;height:2rem;margin-right:0.75rem">
          <slot name="img"></slot>
        </div>
        <div style="font-size:1.5rem">
          <slot name="more"></slot>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {};
</script>

<style scoped>
.right {
  display: flex;
  flex: 1;
  box-sizing: border-box;
}
.left {
  font-size: 3.5em;
  display: flex;
  margin: 0 0.5rem;
}
</style>


