        <span slot="data">MT</span>
    </person>
    <person>
        <div slot="title">微信号</div>
        <span slot="data">mt659</span>
    </person>
    <person>
        <div slot="title">二维码名片</div>
        <span slot="data"></span>
    </person>
    <person>
        <div slot="title">更多</div>
        <span slot="data"></span>
    </person>
    <person>
        <div slot="title">我的地址</div>
        <span slot="data"></span>
    </person>
  </div>
</template>

<script>
import person from './../template/person'
export default {
    components: {
        person
    }
};
</script>

<style>
#about {
  background: #ccc;
}
#about .head {
  height: 4rem;
  line-height: 4rem;
}
#about .back {
    display: inline;
    padding: 0.5rem 1rem;
    font-size: 2em;
}
#about .head {
    line-height: 4rem;
    display: flex;
}
#about img {
    width: 3.5rem;
    height: 3.5rem;
    vertical-align: middle;
}
</style>


