<template>
  <div class="char">
    <div class="img">
      <slot name="char_img"></slot>
    </div>
    <div class="cont">
      <div class="info">
        <div>
          <slot name="name"></slot>
        </div>
        <div style="color:#999;width:100%">
          <slot name="info"></slot>
        </div>
      </div>
      <div class="date">
        <slot name="time"></slot>
      </div>
    </div>
  </div>
</template>

<script>
export default {};
</script>

<style>
.char .cont .date {
  padding: 10px;
  color: #999;
}
.char .cont .info {
  padding-top: 0.5rem;
  line-height: 1.5rem;
  flex: 1;
}
.char .cont {
  flex: 1;
  overflow: hidden;
  display: flex;
}
.char .img {
  width: 3.5rem;
  height: 3.5rem;
  padding: 0.5rem;
}
.char {
  display: flex;
  padding-left: 0.5rem;
  background: #fff;
}
</style>


