<template>
  <div id="index">
    <div>
      <router-view></router-view>
    </div>
    <div class="foot">
      <mt-tabbar v-model="selected">
        <mt-tab-item id="/">
          <div style="position:absolute;left:3.3rem;top:0.5rem">
            <mt-badge size="small" color="red">666</mt-badge>
          </div>
          <i class="iconfont index_icon icon-houtai-icon_huabanfuben"></i>
          <div>会话</div>
        </mt-tab-item>
        <mt-tab-item id="/list">
          <i class="iconfont index_icon icon-tongxunlu1-copy"></i>
          <div>通讯录</div>
        </mt-tab-item>
        <mt-tab-item id="/find">
          <i class="iconfont index_icon icon-faxian"></i>
          <div>发现</div>
        </mt-tab-item>
        <mt-tab-item id="/me">
          <i class="iconfont index_icon icon-wo"></i>
          <div>我</div>
        </mt-tab-item>
      </mt-tabbar>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      selected: "/"
    };
  },
  watch: {
    selected() {
      location.href = "#" + this.selected;
    }
  },
  created() {
    this.selected = this.$route.path;
  }
};
</script>

<style>
#index .foot {
  position: fixed;
  bottom: 0;
  width: 100%;
}
.index_icon {
  display: block;
  padding: 0.3rem 0;
}
.iconfont {
  font-family: "iconfont" !important;
  font-size: 2em;
  font-style: normal;
  -webkit-font-smoothing: antialiased;
  -webkit-text-stroke-width: 0.2px;
  -moz-osx-font-smoothing: grayscale;
}
</style>


