import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

import index from './component/index.vue'
import about from './component/about.vue'
import wechat from './component/children/wechat.vue'
import list from './component/children/list.vue'
import find from './component/children/find.vue'
import me from './component/children/me.vue'

export default new Router({
  routes: [{
    path: '/',
    component: index,
    children: [{
      path: '/',
      component: wechat
    }, {
      path: 'list',
      component: list
    }, {
      path: 'find',
      component: find
    }, {
      path: 'me',
      component: me
    }, ]
  }, {
    path: '/about',
    component: about
  }]
})



